@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap");
#intro_actividad04 {
  display: flex;
  flex-direction: row-reverse;
  background-image: url("/build/img/contenido/Pag04/fondo.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 960px) {
  #intro_actividad04 {
    flex-direction: column;
    justify-content: end;
  }
}
@media (max-width: 768px) {
  #intro_actividad04 {
    height: 50rem;
  }
}
#intro_actividad04::after {
  content: "";
  position: absolute;
  background: #3d3d3d;
  width: 100%;
  height: 35%;
  top: 0;
  left: 0;
  z-index: -1;
}
#intro_actividad04 #vineta04 {
  position: absolute;
  width: 40rem;
  padding: 2rem;
  background: #ffffff;
  border-radius: 2rem;
  border: 5px solid #3b5895;
  top: 8rem;
  left: 25rem;
}
@media (max-width: 1280px) {
  #intro_actividad04 #vineta04 {
    width: 25rem;
    left: 15rem;
  }
}
@media (max-width: 960px) {
  #intro_actividad04 #vineta04 {
    position: static;
    align-self: center;
    margin-bottom: 3rem;
    width: 50%;
  }
}
@media (max-width: 768px) {
  #intro_actividad04 #vineta04 {
    width: 70%;
    font-size: 1.4rem;
  }
}
@media (max-width: 480px) {
  #intro_actividad04 #vineta04 {
    width: 90%;
  }
}
#intro_actividad04 #vineta04 #relativo {
  position: relative;
  text-align: center;
}
#intro_actividad04 #vineta04 #relativo #frase01 {
  color: #3b5895;
  font-weight: 700;
}
#intro_actividad04 #vineta04 #relativo #frase02 {
  color: #129a63;
  font-weight: 700;
}
#intro_actividad04 #vineta04 #relativo::after {
  content: "";
  position: absolute;
  background: #3b5895;
  top: 20%;
  right: -5rem;
  width: 3rem;
  height: 4rem;
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
}
@media (max-width: 960px) {
  #intro_actividad04 #vineta04 #relativo::after {
    right: 40%;
    top: calc(100% + 2.4rem);
    width: 4rem;
    height: 3rem;
    clip-path: polygon(0 0, 50% 100%, 100% 0);
  }
}
#intro_actividad04 .cont-imagen {
  margin-right: 15%;
}
@media (max-width: 960px) {
  #intro_actividad04 .cont-imagen {
    margin: 0;
    height: 50%;
  }
}

